'use client';

import React, { useState, useEffect } from 'react';
import { products } from '@/constants/Constants';
import { useRouter, usePathname, useSearchParams } from 'next/navigation';

const ShowProducts = () => {
    const router = useRouter();
    const searchParams = useSearchParams();
    let category = searchParams.get('category');
    const productId = searchParams.get('id');
    const [imageIndex, setImageIndex] = useState(0);
    const [activeTab, setActiveTab] = useState('description');
    const [newStars, setNewStars] = useState(0);
    const [newName, setNewName] = useState('');
    const [newReview, setNewReview] = useState('');
    const [reviews, setReviews] = useState([
        {
            id: 1,
            name: "John Doe",
            avatar: "/images/john-doe.jpg",
            stars: 5,
            text: "Amazing product! Exceeded my expectations. Highly recommend it to anyone looking for high quality."
        },
        {
            id: 2,
            name: "Jane Smith",
            avatar: "/images/jane-smith.jpg",
            stars: 4,
            text: "Very good product but the sizing was a little off. Still a great purchase overall!"
        },
    ]);

    let product = products.find((prod) => prod.id === parseInt(productId, 10));
    if (!product) {
        return (
            <div className="flex items-center justify-center min-h-screen text-gray-600">
                <h1 className="text-2xl font-bold">Product not found.</h1>
            </div>
        );
    }
    const categoryRanges = {
        men: [1, 20],
        women: [21, 40],
        kids: [41, 60],
        accessories: [61, 80],
      };
    
      // Function to get the category based on the product ID
      const getCategoryFromId = (id) => {
        for (const [category, [start, end]] of Object.entries(categoryRanges)) {
          if (id >= start && id <= end) return category;
        }
        return 'men'; // Default to 'men' if outside of the specified ranges
      };
    
      // Function to get the next product ID and category
      const getNextProduct = (id) => {
        const category = getCategoryFromId(id);
        const [start, end] = categoryRanges[category];
    
        if (id < end) {
          return { id: id + 1, category };
        }
    
        // Move to the next category
        const nextCategory =
          category === 'men'
            ? 'women'
            : category === 'women'
            ? 'kids'
            : category === 'kids'
            ? 'accessories'
            : 'men';
    
        const [nextStart] = categoryRanges[nextCategory];
        return { id: nextStart, category: nextCategory };
      };
    
      // Function to get the previous product ID and category
      const getPrevProduct = (id) => {
        const category = getCategoryFromId(id);
        const [start, end] = categoryRanges[category];
    
        if (id > start) {
          return { id: id - 1, category };
        }
    
        // Move to the previous category
        const prevCategory =
          category === 'men'
            ? 'accessories'
            : category === 'women'
            ? 'men'
            : category === 'kids'
            ? 'women'
            : 'kids';
    
        const [, prevEnd] = categoryRanges[prevCategory];
        return { id: prevEnd, category: prevCategory };
      };
    
      // Navigate to the next product
      const handleNextProduct = () => {
        const { id: nextId, category: nextCategory } = getNextProduct(productId);
        router.push(`/products/${nextCategory === 'men' ? '' : nextCategory}?id=${nextId}`);
      };
    
      // Navigate to the previous product
      const handlePrevProduct = () => {
        const { id: prevId, category: prevCategory } = getPrevProduct(productId);
        router.push(`/products/${prevCategory === 'men' ? '' : prevCategory}?id=${prevId}`);
      };
    
    // Automatically change the background every 3 seconds
    useEffect(() => {
        const interval = setInterval(() => {
            setImageIndex((prevIndex) => (prevIndex + 1) % product.images.length);
        }, 3000);

        return () => clearInterval(interval);
    }, [product.images.length]);

    // Handle image navigation (change image within the same product)
    const handleNextImage = () => {
        setImageIndex((prevIndex) => (prevIndex + 1) % product.images.length);
    };

    const handlePrevImage = () => {
        setImageIndex((prevIndex) => (prevIndex - 1 + product.images.length) % product.images.length);
    };

    // Handle adding a new review
    const handleAddReview = () => {
        // Set the name to 'Anonymous' if not provided
        const reviewName = newName.trim() ? newName : "Anonymous";

        if (newReview && newStars > 0) {
            const newReviewData = {
                id: reviews.length + 1,
                name: reviewName,
                avatar: "/images/default-avatar.jpg", // Default avatar
                stars: newStars,
                text: newReview,
            };
            setReviews((prevReviews) => [...prevReviews, newReviewData]);
            setNewReview("");
            setNewStars(0);
            setNewName(""); // Clear the name input
        } else {
            alert("Please enter a review, select a rating, and provide a name if you want!");
        }
    };

    return (
        <div className="relative w-full min-h-screen">
            {/* Blurred Background */}
            <div
                className="absolute inset-0 bg-cover bg-center transition-all duration-1000"
                style={{
                    backgroundImage: `url(${product.images[imageIndex]})`,
                    filter: 'blur(8px)',
                    zIndex: -1,
                    opacity: 0.6,
                    borderRadius: '16px',
                    border: '8px solid rgba(255, 255, 255, 0.5)',
                    transform: 'scale(1.05)',
                    clipPath: 'inset(0 0 2% 0)',
                }}
            ></div>

            {/* Main Content */}
            <div className="relative flex flex-col items-center justify-center min-h-screen gap-6">
                {/* Navigation Buttons for Products */}
                <div className="absolute top-1/2 left-0 transform -translate-y-1/2 px-4">
                    <button
                        className="text-white text-4xl hover:opacity-70 transition"
                        onClick={handlePrevProduct}
                    >
                        &#9664; {/* Left Button */}
                    </button>
                </div>
                <div className="absolute top-1/2 right-0 transform -translate-y-1/2 px-4">
                    <button
                        className="text-white text-4xl hover:opacity-70 transition"
                        onClick={handleNextProduct}
                    >
                        &#9654; {/* Right Button */}
                    </button>
                </div>

                {/* Main Product Box */}
                <div className="flex flex-wrap bg-white rounded-lg shadow-lg border border-gray-300 max-w-7xl w-full p-6">
                    {/* Left Section */}
                    <div className="flex w-2/3 pr-6">
                        {/* Constant Profile Image */}
                        <div className="w-2/5 flex justify-center items-center p-4">
                            <img
                                src={product.images[0]} // Constant profile image
                                alt={product.title}
                                className="w-full h-[450px] object-cover rounded-lg shadow-md border-2 border-gray-300"
                            />
                        </div>

                        {/* Product Info */}
                        <div className="w-3/5 flex flex-col justify-center p-6">
                            <h1 className="text-4xl font-bold text-gray-800 mb-4">
                                {product.title}
                            </h1>
                            <div className="flex items-center gap-4 mb-6">
                                <span className="text-3xl font-semibold text-gray-900">
                                    {product.price}
                                </span>
                                {product.discount && (
                                    <span className="text-xl line-through text-gray-500">
                                        {product.discount}
                                    </span>
                                )}
                            </div>
                            <p className="text-lg text-gray-700 mb-6">
                                {product.description}
                            </p>
                        </div>
                    </div>

                    {/* Right Section with Image Navigation */}
                    <div className="relative w-1/3 h-[450px] bg-gray-900 rounded-lg overflow-hidden">
                        <div className="w-full h-full flex justify-center items-center bg-black bg-opacity-40">
                            <img
                                src={product.images[imageIndex]} // Changing background image
                                alt="Product Preview"
                                className="w-full h-full object-cover transition-all duration-1000"
                            />
                        </div>

                        {/* Buttons for Image Navigation */}
                        <div className="absolute inset-0 flex items-center justify-between px-4">
                            <button
                                className="text-white text-3xl hover:opacity-70 transition"
                                onClick={handlePrevImage}
                            >
                                &#9664; {/* Left Image Button */}
                            </button>
                            <button
                                className="text-white text-3xl hover:opacity-70 transition"
                                onClick={handleNextImage}
                            >
                                &#9654; {/* Right Image Button */}
                            </button>
                        </div>
                    </div>
                </div>

                {/* Tabs Section */}
               <div className="bg-white p-8 shadow-lg rounded-lg w-full max-w-7xl mx-auto h-[320px] overflow-auto">
            {/* Tab Navigation */}
            <div className="flex space-x-4 mb-4">
                <button
                    onClick={() => setActiveTab('description')}
                    className={`px-4 py-2 ${activeTab === 'description' ? 'text-blue-500 border-b-2 border-blue-500' : 'text-gray-500'}`}
                >
                    Description
                </button>
                <button
                    onClick={() => setActiveTab('additional')}
                    className={`px-4 py-2 ${activeTab === 'additional' ? 'text-blue-500 border-b-2 border-blue-500' : 'text-gray-500'}`}
                >
                    Additional Info
                </button>
                <button
                    onClick={() => setActiveTab('reviews')}
                    className={`px-4 py-2 ${activeTab === 'reviews' ? 'text-blue-500 border-b-2 border-blue-500' : 'text-gray-500'}`}
                >
                    Reviews
                </button>
            </div>

            {/* Description Tab */}
            {activeTab === 'description' && (
                <div className="transition-all duration-700 ease-in-out transform opacity-100">
                    <h3 className="text-xl font-bold mb-2 text-gray-800">Product Details</h3>
                    <p className="mb-4 text-gray-700 leading-relaxed">
                        This premium product is crafted with the utmost attention to detail, ensuring durability and style.
                        Whether you're at work or leisure, its versatile design perfectly complements any setting. Made from
                        high-quality materials, it provides exceptional comfort and long-lasting performance. Experience
                        excellence and elevate your lifestyle with this must-have item.
                    </p>
                    <p className="text-gray-700 leading-relaxed">
                        Designed with innovation in mind, this product incorporates cutting-edge technology to meet modern
                        demands. Its sleek aesthetic and superior functionality make it a standout choice among peers.
                    </p>
                </div>
            )}

            {/* Additional Info Tab */}
            {activeTab === 'additional' && (
                <div className="transition-all duration-700 ease-in-out transform opacity-100">
                    <h3 className="text-xl font-bold mb-2 text-gray-800">Additional Information</h3>
                    <ul className="list-disc ml-5 space-y-2 text-gray-700">
                        <li><span className="font-semibold">Material:</span> Premium-grade cotton and polyester blend for a perfect balance of comfort and durability.</li>
                        <li><span className="font-semibold">Sizes:</span> Available in Small, Medium, Large, and Extra-Large to fit every body type.</li>
                        <li><span className="font-semibold">Care Instructions:</span> Machine washable; tumble dry on low heat for best results.</li>
                        <li><span className="font-semibold">Usage:</span> Ideal for casual outings, office wear, or lounging at home.</li>
                        <li><span className="font-semibold">Warranty:</span> Backed by a 1-year limited warranty for quality assurance.</li>
                    </ul>
                </div>
            )}

            {/* Reviews Tab */}
            {activeTab === 'reviews' && (
    <div className="transition-all duration-700 ease-in-out transform opacity-100">
        <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6">
            {reviews.map((review) => (
                <div
                    key={review.id}
                    className="flex items-start gap-4 bg-gray-100 p-4 rounded-lg shadow-sm"
                >
                    <img
                        src={review.avatar}
                        alt={review.name}
                        className="w-12 h-12 rounded-full shadow-md"
                    />
                    <div>
                        <h4 className="font-semibold text-gray-800">{review.name}</h4>
                        <div className="flex items-center text-yellow-400 mb-1">
                            {'â˜…'.repeat(review.stars)}{' '}
                            {'â˜†'.repeat(5 - review.stars)}
                        </div>
                        <p className="text-gray-600 text-sm">{review.text}</p>
                    </div>
                </div>
            ))}
        </div>
        <div className="flex gap-4 mt-4">
            <div className="flex items-center gap-2">
                <span className="font-semibold text-gray-800">Rate this product:</span>
                {[1, 2, 3, 4, 5].map((star) => (
                    <button
                        key={star}
                        onClick={() => setNewStars(star)}
                        className={`text-2xl ${
                            newStars >= star ? 'text-yellow-400' : 'text-gray-300'
                        }`}
                    >
                        â˜…
                    </button>
                ))}
            </div>
            <input
                type="text"
                placeholder="Write a review..."
                value={newReview}
                onChange={(e) => setNewReview(e.target.value)}
                className="flex-1 border border-gray-300 rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-sky-500"
            />
            <input
                type="text"
                placeholder="Your name (Optional)"
                value={newName}
                onChange={(e) => setNewName(e.target.value)}
                className="border border-gray-300 rounded-lg px-4 py-2 focus:outline-none focus:ring-2 focus:ring-sky-500"
            />
            <button
                onClick={handleAddReview}
                className="bg-sky-500 text-white px-6 py-2 rounded-lg hover:bg-sky-600 transition"
            >
                Add Review
            </button>
        </div>
    </div>
)}

        </div>
            </div>
        </div>
    );
};

export default ShowProducts;
